.home{
  background: url("../img/homebg.jpg");
  width: 100%;
  height: calc(100vh - 130px);
  background-repeat: no-repeat;
  background-position: center;
}
.forintro{
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100%;
    h1{
      font-size: 90px;
      color: #fff;
      margin: 20px 0;
    }
}
.justtest{
   text-align: center;
   color:#deaa86;
   font-size: 24px;
   letter-spacing: 16px;
   margin-top: -10px;
 }
.divider{
  margin: 30px 0;
    .lineone{
      width: 50px;
      height: 1px;
      background-color: #deaa86;
      margin: 3px auto;
    }
    .linetwo{
      width: 200px;
      height: 1px;
      background-color: #deaa86;
      margin: 3px auto;
    }
}
.fastinfo{
     color:#fff;
     padding: 15px;
     letter-spacing: 0.03em;
     font-size: 16px;
     margin-bottom: 20px;
 }
.explore {
  width: 170px;
  text-decoration: none;
  color:#fff;
  display: block;
  text-align: center;
  margin:0 auto;
  cursor: pointer;
  border: 1px solid transparent;
  white-space: nowrap;
  font-size: 14px;
  line-height: 1.42857143;
  padding: 15px 30px;
  letter-spacing: 0.5px;
  border-color: #deaa86;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
  text-transform: uppercase;
}
.explore:hover {
  color: #374853;
  border-color: #deaa86;
  background-color: #deaa86;
}
.aviability {
  width: 100%;
  height: 200px;
  background-color: #374853;
}